export function D1P1(input){
    let nums = input.trim().split('\n')//.
    nums.forEach(num => {if(!RegExp('^[0-9]{1,4}\n?$').test(num)) throw "Invalid Input"});
    nums = nums.map(x => parseInt(x.trim()));
    const numkeys = nums.reduce((dict, key) => Object.assign(dict, {[key]: null}), {})
    for (let x of nums) {
        let find = 2020 - x
        if (numkeys[find] === null){
            return(find * x)
        }
    }
}